export const Face = props => {
  const faceWidth = 42;
  const faceHeight = 42;
  let scale = props.width / squareWidth; // / props.n;

  if (props.width < squareWidth) {
    scale = props.width / squareWidth;
  }
  if (props.width < 40) {
    scale *= 1.3;
  }

  if (faceWidth * scale < 18) {
    return null;
  }

  let faceClassName = '';
  if (props.n > 1) {
    faceClassName = `mask${props.index}${props.n}`;
  }

  return (
    <use
      className={faceClassName}
      xlinkHref={`#${props.face}`}
      transform={`translate(${(-faceWidth * scale) / 2 +
        (props.deltaX * scale) / 2},${(-faceHeight * scale) / 2}) scale(${scale})`}
    />
  );
};
